<template>
  <v-app>
    <component :is="layout">
      <router-view></router-view>
    </component>
  </v-app>
</template>


<script>
// import moment from "moment";
import {mapState } from 'vuex';
import SessionUtil from '@/session';
import moment from 'moment';
const default_layout = "default";

export default {
  data: () => ({
    timer_fn: null,
  }),
  async created() {
    await this.isSessionActive();
  },
  computed: {
    ...mapState({
      jwt: 'jwt'
    }),
    layout() {
      return (this.$route.meta.layout || default_layout) + "-layout";
    }
  },
  methods: {
    async isSessionActive(){
      if (this.jwt != "" ){
        let jwtHelper = new SessionUtil(this.jwt);
        let stillActive = moment(moment()).isSameOrBefore(jwtHelper.getExpiration());
        if (!stillActive && (!Number.isNan(jwtHelper.getExpiration()))){
          this.$store.dispatch('unset_token');
        }
      }
      else {
        this.$router.push('/login');
      }
    }
  }
};
</script>

