<template>
  <v-snackbar v-model="snackbar" :bottom="true" :multi-line="true" :timeout="0" :color="color">
    <v-layout row wrap align-center>
      <v-flex d-flex>
        <span class="mr-1 mt-3">
          <strong v-if="title != ''">{{title}}</strong>
        </span>
        <p class="mt-3">{{ text }}</p>
        <v-btn dark flat @click="$store.commit('HIDE_TOAST')">Cerrar</v-btn>
      </v-flex>
    </v-layout>
  </v-snackbar>
</template>

<script>
export default {
  name: "toast",
  props: ["text", "appear", "timeout", "color", "title"],
  computed: {
    snackbar: {
      get: function() {
        return this.appear === true ? true : false;
      },
      set: function(newVal) {}
    }
  }
};
</script>

