import jwt_decode from 'jwt-decode'
import moment from 'moment';

export default class Session{

    constructor(jwt){
        this.original = jwt;
        this.jwt = jwt_decode(jwt);
    }

    getExpiration(){
        return moment.unix(this.jwt.exp);
    }

    getJWT(){
        return {
            decoded: this.jwt,
            original: this.original
        };
    }

}