import Vue from 'vue';
import Vuex from 'vuex';
import SessionUtil from '@/session';
import router from '../router';

Vue.use(Vuex);
var $http = Vue.prototype.$http;

export default new Vuex.Store({
    // Get data, mapState
    state: {
        alert: {
            appear: false,
            type: "red",
            message: 'Testing',
            timeout: 4000
        },
        toast: {
            appear: false,
            color: "success",
            message: "I'm snackbar",
            title: '',
            timeout: 6000
        },
        loadingPage: {
            appear: false,
            message: '',
        },
        user: JSON.parse(localStorage.getItem('app-suuma-user')) || {},
        photo: {},
        timeToExp: 0,
        jwt: localStorage.getItem('app-suuma') || '',
    },
    // Get data with specific format, mapGetters
    getters: {
        isLoggedIn: (state) => {
            if (typeof state.jwt === 'undefined' || state.jwt == "undefined" || state.jwt == null || state.jwt == '')
                return false
            return true
        }
    },
    // Modify the data
    mutations: {
        CHANGE_ALERT(state, payload) {
            state.alert.appear = payload.appear || false;
            state.alert.type = payload.type || 'red darken-5';
            state.alert.message = payload.message || '';
            state.alert.timeout = payload.timeout || 4000;
        },
        SET_LOADING(state, payload) {
            state.loadingPage = payload;
        },
        SET_TOAST(state, payload) {
            state.toast.appear = payload.appear || false;
            state.toast.color = payload.color || 'success';
            state.toast.message = payload.message || "";
            state.toast.title = payload.title || '';
            state.toast.timeout = payload.timeout || 6000;
        },
        HIDE_TOAST(state) {
            state.toast.appear = false;
        },
        subs_exp(state, payload) {
            state.timeToExp -= payload;
        },
        set_profile_photo(state, payload) {
            state.photo = payload.photo;
        },
        set_exp(state, payload) {
            state.timeToExp = payload;
        },
        auth_success(state, payload) {
            state.jwt = payload.token;
            state.user = payload.user;
        },
        auth_error(state, err) {
            state.alert.appear = true;
            state.alert.message = JSON.stringify(err);
        },
        auth_logout(state) {
            state.alert.appear = true;
            state.alert.message = "Hasta la prxima.";
        },
        auth_logout_err(state, error) {
            state.alert.appear = true;
            state.alert.message = "Ocurri un error. StackTrace:" + JSON.stringify(error);
        }
    },
    // Asynchronous operations
    actions: {
        async isApiActive() {
            return $http.get('/');
        },
        decode({}, token) {
            let SessionHelper = new SessionUtil(token);
            return SessionHelper.getJWT().decoded;
        },
        set_token({}, payload) {
            localStorage.setItem('app-suuma', payload.token);
            localStorage.setItem('app-suuma-user', JSON.stringify(payload.user));
            $http.defaults.headers.common['Authorization'] = 'Bearer ' + payload.token;
        },
        unset_token() {
            localStorage.removeItem('app-suuma');
            localStorage.removeItem('app-suuma-user');
            delete $http.defaults.headers.common['Authorization'];
            this.state.jwt = "";
            this.state.user = {};
            this.state.photo = {};
        },
        error_handler({
            commit
        }, payload) {
            let error = payload.errorCode.split('_')[1];
            let type = '';
            switch (error) {
                case 'wrong-user-password':
                    type = "error";
                    break;
                case 'not-active':
                    type = "warning"
                    break;
                default:
                    type = "error"
                    break;
            }
            commit('auth_error', payload.status + ': ' + payload.message);
            commit('CHANGE_ALERT', {
                appear: true,
                type: type,
                message: payload.status + ': ' + payload.message,
            });
        },
        async toast_me({commit}, payload){
            // console.log(payload);
            
            commit('SET_TOAST', {
                appear: payload.appear || false,
                color: payload.color || 'success',
                message: payload.message ||'',
                title: payload.title ||'',
            });
            window.setTimeout(function(){
                commit('HIDE_TOAST');
            }, payload.timeout || 6000);
        },
        async fetch_photo({commit}, req) {
            let photo = await $http.get("profile/photo");
            commit('set_profile_photo', {
                photo: photo.data
            });
        },
        async login({
            dispatch,
            commit
        }, req) {
            try {
                commit('SET_LOADING', {
                    appear: true,
                    message: "Trabajando..."
                });
                let response = await $http.post("/login", req);
                if (response.data.statusCode != 200) {
                    dispatch('error_handler', response.data);
                } else {
                    let userInfo = response.data.data || null;
                    this.state.user = userInfo.user;
                    await dispatch('set_token', userInfo);
                    commit('auth_success', userInfo);
                    await dispatch('fetch_photo');
                    if (this.state.user.profile.photo === "" || this.state.user.profile.photo === null) {
                        dispatch('toast_me', {
                            appear: true,
                            color: 'grey darken-4',
                            message: 'Tienes imagen de perfil por defecto. Cmbiala por tu foto favorita.'
                        })
                    }
                }
            } catch (err) {
                if (err.response !== undefined) {
                    let error = err.response.data;
                    commit('auth_error', err.response.status + ':' + error.message);
                    commit('CHANGE_ALERT', {
                        appear: true,
                        message: err.message
                    });
                    await dispatch('unset_token');
                } else {
                    commit('CHANGE_ALERT', {
                        appear: true,
                        message: "No se puede conectar con la DB. Revise que el API est encendida."
                    });
                }
            } finally {
                commit('SET_LOADING', {
                    appear: false,
                    message: ""
                })
            }
        },
        async refresh({
            commit,
            dispatch
        }) {
            // TODO: Not ready because of the changes in the response
            try {
                let response = $http.post('/refresh');
                let token = response.data;
                dispatch('set_token', token);
            } catch (err) {
                console.log(err);
                commit('auth_error', err);
                commit('CHANGE_ALERT', {
                    appear: true,
                    type: 'red darken-4',
                    message: err.response.data.message
                });
                localStorage.removeItem('app-suuma');
                router.push('/login');
            }
        },
        async logout({
            dispatch,
            commit
        }) {
            try {
                let outFlag = await $http.get("/logout");
                console.log(outFlag);
                commit('auth_logout');
                dispatch('unset_token');
                this.state.user = {};
                this.state.photo = {};
            } catch (err) {
                console.log(err);
                commit('auth_logout_err', err);
            }
        }
    }
})