import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/pages/Home.vue';
import store from '../store';
import SessionUtil from '@/session';
import moment from 'moment';

Vue.use(Router);

let router = new Router({
    mode: 'history',
    routes: [{
            path: '/',
            name: 'Home',
            meta: {
                layout: 'default'
            },
            component: Home,
            meta: {
                requiresAuth: true
            }
        },
        {
            path: '/perfil',
            name: 'Perfil',
            component: () => import('@/pages/Profile/Profile.vue'),
            meta: {
                requiresAuth: true
            }
        },
        {
            path: '/guardias',
            name: 'Guardias',
            component: () => import('@/pages/Guardias.vue'),
            meta: {
                requiresAuth: true
            }
        },
        {
            path: '/aprobar',
            name: 'Aprobaciones',
            component: () => import('@/pages/Aprobar/Default.vue'),
            meta: {
                requiresAuth: true
            },
            children: [
                {
                    path: 'usuarios',
                    component: () => import('@/pages/Aprobar/Usuarios.vue')
                }
            ]
        },
        {
            path: '/acerca',
            name: 'About',
            meta: {
                layout: 'default'
            },
            component: () => import('@/pages/About.vue'),
            meta: {
                requiresAuth: true
            }
        },
        {
            path: '/registro',
            name: 'Registro',
            meta: {
                layout: 'login'
            },
            component: () => import('@/pages/Register/Register.vue')
        },
        {
            path: '/login',
            name: 'Login',
            meta: {
                layout: 'login'
            },
            component: () => import('@/pages/Login/Login.vue')
        },
        {
            path: '/404',
            name: "Not-Found",
            meta: {
                layout: 'login'
            },
            component: () => import('@/pages/default/404.vue')
        },
        {
            path: '*',
            redirect: '/404'
        }
    ]
});

router.beforeEach((to, from, next) => {
    store.commit('CHANGE_ALERT', {
        appear: false
    });
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (store.getters.isLoggedIn) {
            retrieveTime();
            next()
            return
        }
        next('/login');
    }
    next();
});

var timer_fn;
Vue.prototype.timer_fn = null;

function retrieveTime() {
    let expiration = new SessionUtil(store.state.jwt);
    let now = moment();
    let exp = expiration.getExpiration();
    let stillActive = moment(now).isSameOrBefore(exp);
    if (!stillActive && (!Number.isNaN(exp))) {
        store.dispatch("unset_token");
    } else {
        store.commit('set_exp', exp.diff(now))
        // changeTime(exp.diff(now));
        window.setTimeout(timer, 1000);
    }
}

function timer() {
    window.clearTimeout(Vue.prototype.timer_fn);
    store.commit('subs_exp', 1000);
    // changeTime(1000);
    Vue.prototype.timer_fn = window.setTimeout(timer, 1000);
}

export default router;