import Vue from 'vue';
import axios from 'axios';

let url = '';
if (process.env.NODE_ENV == 'production') {
    url = 'http://ssintranet.iberomed.org/api';
}
else {
    url = 'http://localhost:8000/api';
}
const instance = axios.create({
    baseURL: url
})

instance.defaults.headers.common['Authorization'] = "Bearer " + localStorage.getItem('app-suuma');

Vue.prototype.$http = instance;