<template>
    <div>
        <v-alert :value=appear :color=alertType type="warning" :timeout=timeout :multi-line=false dismissible>
            {{ message }}
            <!-- <v-btn flat outline @click="hideAlert">Cerrar</v-btn> -->
        </v-alert>
    </div>
</template>

<script>
export default {
    props: {
        appear: {
            type: Boolean,
            default: false,
        },
        message: {
            type: String,
            required: true,
        },
        alertType: {
            type: String,
            default: 'red darken-5'
        },
        timeout: {
            type: Number,
            default: 4000
        }
    },
    methods:{
        hideAlert(){
            this.$store.commit('CHANGE_ALERT', {appear: false, type: 'info', message: ''});
        }
    }
}
</script>

<style>
.v-alert{
    margin: 0px !important;
}
</style>

